.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 0s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.border {
  border: 1px solid grey;
}

.col {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  flex-direction: row;
}

.w25p {
  width: 25%;
}

.w100p {
  width: 100%;
}

.w80p {
  width: 70%;
}

.input {
  width: 70%;
  height: 25px;
  background-color: rgb(246, 246, 246);
  border-radius: 1px;
  border: 1px solid rgb(214, 214, 214);
  font-size: 9px;
  margin:5px;
  padding-left: 5px;
}

.button {
  width: 70%;
  background-color: rgb(0, 183, 255);
  border: 1px solid rgb(214, 214, 214);
  border-radius: 1px;

}

.c1 {
  background-color: red;
}

.img {
  max-width: 100%;
  height: auto;
}

.center {
  justify-content: center;
  align-items: center;
}

.h100w {
  height: 100vh;
}
